export default {
    brandName: function(state){
    	return state.BrandName
    },
    stempreading: function(state){
        if((state.sTempReading[3]*256 + state.sTempReading[4])>0x7fff){
            return (state.sTempReading[3]*256 + state.sTempReading[4] - 65536)/10
        }else{
            return (state.sTempReading[3]*256 + state.sTempReading[4])/10
        }
    },
    ftempsetting: function(state){
        if((state.fTempSetting[4]*256 + state.fTempSetting[5])>0x7fff){
            return (state.fTempSetting[4]*256 + state.fTempSetting[5] - 65536)/10
        }else{
            return (state.fTempSetting[4]*256 + state.fTempSetting[5])/10
        }
    	/*return (state.fTempSetting[4]*256 + state.fTempSetting[5])/10*/
    },
    scountdownreading: function(state){
        var a = state.sCountdownReading
        var tot = a[3]*256+a[4]
        if(tot>0 && tot){
            return {
                h: parseInt(tot/60),
                m: parseInt(tot%60)
            }
        }else{
           return {
                h: 0,
                m: 0
            } 
        }
    },
    pid1p: function(state){
    	return (state.fP1[4]*256 + state.fP1[5])
    },
    pid1i: function(state){
    	return (state.fI1[4]*256 + state.fI1[5])
    },
    pid1d: function(state){
    	return (state.fD1[4]*256 + state.fD1[5])
    },
    pid2p: function(state){
    	return (state.fP2[4]*256 + state.fP2[5])
    },
    pid2i: function(state){
    	return (state.fI2[4]*256 + state.fI2[5])
    },
    pid2d: function(state){
    	return (state.fD2[4]*256 + state.fD2[5])
    },
    pid3p: function(state){
    	return (state.fP3[4]*256 + state.fP3[5])
    },
    pid3i: function(state){
    	return (state.fI3[4]*256 + state.fI3[5])
    },
    pid3d: function(state){
    	return (state.fD3[4]*256 + state.fD3[5])
    },
    pid4p: function(state){
    	return (state.fP4[4]*256 + state.fP4[5])
    },
    pid4i: function(state){
    	return (state.fI4[4]*256 + state.fI4[5])
    },
    pid4d: function(state){
    	return (state.fD4[4]*256 + state.fD4[5])
    },
    pid5p: function(state){
    	return (state.fP5[4]*256 + state.fP5[5])
    },
    pid5i: function(state){
    	return (state.fI5[4]*256 + state.fI5[5])
    },
    pid5d: function(state){
    	return (state.fD5[4]*256 + state.fD5[5])
    },
    pid6p: function(state){
    	return (state.fP6[4]*256 + state.fP6[5])
    },
    pid6i: function(state){
    	return (state.fI6[4]*256 + state.fI6[5])
    },
    pid6d: function(state){
    	return (state.fD6[4]*256 + state.fD6[5])
    },
    pid7p: function(state){
    	return (state.fP7[4]*256 + state.fP7[5])
    },
    pid7i: function(state){
    	return (state.fI7[4]*256 + state.fI7[5])
    },
    pid7d: function(state){
    	return (state.fD7[4]*256 + state.fD7[5])
    },
    pid8p: function(state){
    	return (state.fP8[4]*256 + state.fP8[5])
    },
    pid8i: function(state){
    	return (state.fI8[4]*256 + state.fI8[5])
    },
    pid8d: function(state){
    	return (state.fD8[4]*256 + state.fD8[5])
    }

}


