<template>
	<div id="app_work_bottom">
		
		<div class="work_bottom_section">
			<div class="gert">
				<div class="part_left">
					<div class="jiare">
						<img v-if='jiarestate' src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/jiarekai.png"/>
						<img v-else src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/jiareguan.png"/>
						<div class="btn jiarebtn" :class="jiarestate?'down':''" @touchend='jiare'>
							<div class="coo"></div>
						</div>
						加热
					</div>
					<div class="fengji">
						<img v-if='fengjistate' src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/fengjikai.png"/>
						<img v-else src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/fengjiguan.png"/>
						<div class="btn jiarebtn" :class="fengjistate?'down':''" @touchend='fengji'>
							<div class="coo"></div>
						</div>
						风机
					</div>
				</div>
				<div class="part_right">
					<div class="dingshi">
						<p>定时</p>
						<div class="thechoose">
							<input type="text" ref='sethinput' @blur="sethinputblur()" placeholder="<100"> 小时 <input type="text" ref='setminput' @blur="setminputblur()"> 分钟
						</div>
						<div class="sub" @touchend="subsettime()">确定</div>
					</div>
					<div class="wenduxiuzheng">
						<!-- <p>温度修正</p>
						<div class="thechoose">
							<label for="">零点: </label> <input type="text" ref='setlingdianinput' @blur="setlingdianinputblur()" placeholder="-9.9~9.9">
						</div>
						<div class="thechoose">
							<label for="">满度: </label> <input type="text" ref='setmanduinput' @blur="setmanduinputblur()" placeholder="-9.9~9.9">
						</div> -->
						<p>温度设定</p>
						<div class="thechoose">
							SV: <input type="text" ref='settempinput' @blur="settempinputblur()" placeholder="-999.0~999.0"> ℃
						</div>
						<div class="sub" @touchend="subsettemp()">确定</div>
					</div>
				</div>
			</div>
		</div>

		<!-- <div class="postkai">
			<img src="http://www.ouzhongiot.com/webpage/7A00/7A31A/img/kaibtn.png" @click="guan"/>
		</div> -->
		
	</div>
</template>
<script>


export default {
	data () {
        return {
        }
    },
	computed:{
		/*stempreading:function(){
			return this.$store.getters.stempreading
		},
		ftempsetting:function(){
			return this.$store.getters.ftempsetting
		},*/
		jiarestate:function(){
			return this.$store.state.fWarmSwitch[4]
		},
		fengjistate:function(){
			return this.$store.state.fFanSwitch[4]
		}
	},
	methods:{
		guan:function(){
			this.$store.dispatch("guan");
		},
		jiare:function(){
			/*console.log(this.$store.state.fTempSetting)*/
			this.$store.dispatch("jiare");
		},
		fengji:function(){
			/*console.log(this.fengjistate)*/
			this.$store.dispatch("fengji");
		},
		sethinputblur:function(){
			var h = this.$refs.sethinput.value;
			if(h<=99&&h>=0&&h){
				this.$refs.sethinput.value=parseInt(h);
			}else{
				this.$refs.sethinput.value='';
			}
		},
		setminputblur:function(){
			var m = this.$refs.setminput.value
			if(m<=59&&m>=0&&m){
				this.$refs.setminput.value=parseInt(m);
			}else{
				this.$refs.setminput.value='';
			}
		},
		subsettime:function(){
			var h = this.$refs.sethinput.value;
			var m = this.$refs.setminput.value;

			var hsub = (h<100&&h>0&&h)? parseInt(h):0,msub = (m<60&&m>0&&m)? parseInt(m):0;
			/*console.log(hsub +'gg'+ msub)*/
			this.$store.dispatch("durtime",hsub*60+msub);
		},

		settempinputblur:function(a){
			var a = this.$refs.settempinput.value;
			if(a<=350 && a>=-50 && a){
				var tempsub = parseFloat(a).toFixed(1);
            }else{
            	this.$refs.settempinput.value = ''
            }
		},
		subsettemp:function(){
			var temp = this.$refs.settempinput.value;
			var tempsub = (temp<=999&&temp>=-999&&temp)? parseInt(temp):0;
			this.$store.dispatch("wenduset",tempsub);
		},


		/*setlingdianinputblur:function(a){
			var a = this.$refs.setlingdianinput.value
			if(a<=9.9 && a>=-9.9 && a){
                var lingdiansub = parseFloat(a).toFixed(1)
                this.$store.dispatch("lingdian",lingdiansub);
                console.log(lingdiansub)
            }else{
            	this.$refs.setlingdianinput.value = ''
            }
		},
		setmanduinputblur:function(a){
			var a = this.$refs.setmanduinput.value
			if(a<=9.9 && a>=-9.9 && a){
                var mandusub = parseFloat(a).toFixed(1)
                this.$store.dispatch("mandu",mandusub);
                 console.log(mandusub)
            }else{
            	this.$refs.setmanduinput.value = ''
            }
		}*/
	},
	components:{
		
	}
}
</script>

<style>
#app_work_bottom{
	position: absolute;
    width: 100%;
    height: 51%;
    bottom: 0;
}
.work_bottom_section{
    position: relative;
    min-height: 200px;
    height: 78.5%;
    padding: 0 .6rem;
    margin: 0 auto;
    color: #1b4a5d;
    text-align: center;
    font-size: .6rem;
}
.work_bottom_section>div{
	width: 100%;
	height: 100%;
	border-radius: 10px;
	position: relative;
	overflow: hidden;
}
.work_bottom_section>div>div{
	height: 100%;
}
.work_bottom_section .part_left{
	float: left;
	width: 40%;
	margin-left: 0px;
	background: #eff4f7;
}
.work_bottom_section .part_left>div{
	width: 100%;
	height: 50%;
	padding: 0.85rem
}
.work_bottom_section .part_left>div>img{
	height: 1.28rem;
	margin-bottom: .3rem;
}
.work_bottom_section .part_left>div>.btn{
	height: 1.62rem;
	width: 3.925rem;
	background: #fff;
	margin: 0 auto .4rem;
	padding-top: .4rem;
	box-shadow: 0 3px 4px 0px rgba(0,0,0,.2),inset 0 -.7rem .8rem 0px #dedede,inset 0 2px 2px 0px #fff;
}
.work_bottom_section .part_left>div>.btn .coo{
	width: 16px;
	height: 5px;
	margin: 0px auto;
	background: #c3c3c3;
	border-radius: 2px;
}
.work_bottom_section .part_left>div>.btn.down{
	box-shadow: 0 2px 2px 0px rgba(0,0,0,.2),inset 0 -1rem 1rem 0px #dedede,inset 0 1px 1px 0px #fff;
}
.work_bottom_section .part_left>div>.btn.down .coo{
	background: #20c43f;
}
.work_bottom_section .part_right{
	width: 60%;
	float: right;
	margin-right: -1px;
}
.work_bottom_section .part_right>div{
	background: #eff4f7;
	width: 100%;
	height: 50%;
	text-align: left;
	font-size: 12px;
	padding:.34rem 0 0;
	color: #8c9fa8
}
.work_bottom_section .part_right>.dingshi{
	margin: 0px 0 1px 0;
}
.work_bottom_section .part_right>.dingshi input{
	text-align: right;
}
.work_bottom_section .part_right>.wenduxiuzheng{
	margin: 0px 0 0px 0;
}
.work_bottom_section .part_right>div>p{
	margin-left: .68rem;
	font-size: 16px;
	color: #002e45
}
.work_bottom_section .part_right>div>.sub{
	margin: 0 auto;
	width: 3rem;
	height: 1.28rem;
	line-height: 1.28rem;
	color: #002e45;
	text-align: center;
	border: 1px solid #6a8997;
	border-radius: 4px;
	margin-top: .8rem;
}
.work_bottom_section .part_right>.dingshi input{
	width: 2.39rem;
	height: 1.536rem;
	background: #c5effb;
	vertical-align: bottom;
	padding: 0 6px;
	box-shadow:inset 0 3px 3px 0px #b9e5ee,inset 1px 0px 1px 0px #b9e5ee,inset -1px 0px 1px 0px #b9e5ee;
}
.work_bottom_section .part_right>.wenduxiuzheng input{
	width: 4rem;
	height: 1.536rem;
	background: #c5effb;
	vertical-align: bottom;
	padding-left: 6px;
	box-shadow:inset 0 3px 3px 0px #b9e5ee,inset 1px 0px 1px 0px #b9e5ee,inset -1px 0px 1px 0px #b9e5ee;
	vertical-align: middle;
}
.work_bottom_section .part_right>div .thechoose{
	text-align: center;
	margin: 0.5rem 0 0.64rem 0;
}
.work_bottom_section .part_right>div .thechoose label{
	font-size: 14px;
	margin-right: 6px;
}
.postkai{
	margin-top: .6rem;
	height: 2.61rem;
	padding: .4rem 0;
	width: 100%;
	position: absolute;
	bottom: 0;
	background: linear-gradient(to top, #015062,#0e90ad) !important;
	background: -webkit-linear-gradient(top, #0e90ad,#015062) !important;
	background: -o-linear-gradient(top, #0e90ad,#015062) !important;
	background: -moz-linear-gradient(top, #0e90ad,#015062) !important;
	text-align: center;
}
.postkai img{
	width: 1.6rem;
}

</style>



