<template>
	<div id="app">
		<top-nav :setactive = 'setactive' v-on:changesetactive='changesucc'></top-nav>
		<div class="app_content">
			<app-closeblack v-show="kaiguanstate==1"></app-closeblack>
			<div class="app-work">
				<app-work-top></app-work-top>
				<app-work-bottom></app-work-bottom>
			</div>
      <div class="app-work-set" :class="setactive==1?'active':''">
        <pid-set></pid-set> 
      </div>
		</div>
    <set-ting v-show = "setting == 1" :setactive = 'setactive' v-on:changesetactive='changesucc'></set-ting>
  </div>
</template>



<script>
	
import topNav from './topNav.vue'
import appCloseblack from './appCloseblack.vue'
import appWorkTop from './appWorkTop.vue'
import appWorkBottom from './appWorkBottom.vue'
import pidSet from './pidSet.vue'
import setTing from './setting.vue'

var appWorkBottom_child = appWorkBottom.components

export default {
  data () {
    return {
      setactive:0,
    }
  },
  mounted(){
    this.$store.dispatch("initinfo")
  },
  computed:{
  	kaiguanstate : function(){
    	return this.$store.getters.kaiguanstate
    },
    thesetactive : function(){
      return this.setactive
    },
    setting : function(){
      return this.$store.state.setting
    },
  },
  methods:{
    changesucc:function(aa){
      this.setactive=aa
    },
  },
  components: {
  	topNav,
  	appCloseblack,
  	appWorkTop,
  	appWorkBottom,
    pidSet,
  	appWorkBottom_child,
    setTing
  }
}
</script>

<style>
html{
	width: 100%;
	height: 100%;
  overflow: hidden;
}
body{
	width: 100%;
	height: 100%;
	color: #000;
	overflow: hidden;
}
#app {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #eee
}
.app_content {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 100%;
}
.app-work {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-top: 2.1rem;
  overflow: hidden;
  background: url(http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/bg.png) 50% no-repeat;
  background-size: cover;
  -webkit-backface-visibility:hidden;
}
.app-work-set {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-top: 2.1rem;
  background: url(http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/bg.png) 50% no-repeat;
  background-size: cover;
  left: 100%;
  -webkit-backface-visibility:hidden;
  transition: transform .3s linear;
  -webkit-transition: transform .3s linear;
}
.app-work-set.active {
  transform: translateX(-100%);
  -webkit-transform: translateX(-100%);
}
::-webkit-input-placeholder{ 
　　color:#ddd !important;
    font-size: 12px;
}
</style>



