<template>
	<div id="app_close">
		<div class="postguan">
			<img src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/guanbtn.png" @click="kai"/>
		</div>
	</div>
</template>

<script>

export default {
	methods:{
		kai:function(){
			this.$store.dispatch("kai");
		}
	}
}
</script>

<style>
#app_close{
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
    background: rgba(0,0,0,.6);
    z-index: 100;
}

.postguan{
	height: 2.61rem;
	padding: .4rem 0;
	width: 100%;
	position: absolute;
	bottom: 0;
	background: linear-gradient(to top, #015062,#0e90ad) !important;
	background: -webkit-linear-gradient(top, #0e90ad,#015062) !important;
	background: -o-linear-gradient(top, #0e90ad,#015062) !important;
	background: -moz-linear-gradient(top, #0e90ad,#015062) !important;
	text-align: center;
}
.postguan img{
	width: 1.6rem;
}
</style>



