<template>
	<div id="app_work_top">
		<div class="cycle">
			<div class="cycletop">
				<div class="cycletop-txt1">
					<span>PV:<span>{{ stempreading }}</span>℃</span>
				</div>
				<div class="cycletop-txt2">
					{{ scountdownreading.h }}:{{ scountdownreading.m }}
				</div>
			</div>
			<div class="cyclebottom">
				<div class="cyclebottom-txt1">
					<span>SV: <span >{{ ftempsetting }}</span> ℃</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	data () {
		return {
		}
	},
	computed:{
		stempreading:function(){
			return this.$store.getters.stempreading
		},
		ftempsetting:function(){
			return this.$store.getters.ftempsetting
		},
		scountdownreading:function(){
			return this.$store.getters.scountdownreading
		}
	},
	methods:{

	}
	
}
</script>

<style>
#app_work_top{
	width: 100%;
	height: 33.3%;
    margin: 0 auto;
	padding: 0 1rem;
	position: relative;
}
#app_work_top .cycle{
	width: 30rem;
	height: 30rem;
	border-radius: 100%;
	position: absolute;
	bottom: 0;
	left: 50%;
	margin-left:-15rem;
	padding-top: 23rem;
	box-shadow: 0 0px 80px 1px rgba(0,0,0,.16);
	text-align: center;
}
#app_work_top .cycle>div{
	color: #1b4a5d;
}
#app_work_top .cycle>div>div{
	margin-bottom: .3rem;
}
#app_work_top .cycle .cycletop .cycletop-txt1>span{
	font-size: .87rem;
	padding: 0 .6rem 4px;
	border-bottom: 1px solid #002a40;
}
#app_work_top .cycle .cycletop .cycletop-txt1>span span{
	font-size: 3rem;
	font-family:arial;
}
#app_work_top .cycle .cycletop .cycletop-txt2{
}
#app_work_top .cycle .cyclebottom{
	position: relative;
	bottom: -.8rem;
	font-size: .7rem;
}
#app_work_top .cycle .cyclebottom .cyclebottom-txt1{
	height: 18px;
}
#app_work_top .cycle .cyclebottom .cyclebottom-txt1>span{
	padding: 0 1rem 4px;
}
#app_work_top .cycle .cyclebottom .cyclebottom-txt1>span span{
	text-decoration: underline;
}
#app_work_top .cycle .cyclebottom .cyclebottom-txt1 input{
	width: 74px;
	text-align: center;
	border-bottom: 1px solid #000;
}
</style>



