<template>
	<div id="set_ting">
		<div class="setting_xuanzhe">
			<div class="setting_xuanzhe_bg" @touchend = "settingoc"></div>
			<div class="subagain" v-show="gg==1">
				<p><span>提示</span>是否移除设备</p>
				<div class="yn">
					<span class="no" @touchend='delno'>取消</span>
					<span class="yes" @touchend='delyes'>确定</span>
				</div>
			</div>
			<div class="settingdiv">
				<!-- noline -->
				<div> 
					<div class="xsk_tit">
						<p @touchend='msetactive'>管理界面</p>
					</div>
				</div>
				<!-- inline -->
				<!-- <div v-if="authlist!=''">
					<div v-for="amsg in authlist" class="xsk_tit">
						<p v-if="amsg.url == 'LOCAL_SETUP'" @touchend='msetactive'>{{ amsg.name }}</p>
						<p v-else><a :href="Servieceip + 'smarthome/' + amsg.url">{{ amsg.name }}</a></p>
					</div>
				</div>
				
				<div class="xsk_tit">
					<p @touchend='del'>删除设备</p>
				</div> -->
				
				<div class="xsk_submit" @touchend = "settingoc">
					取消
				</div>
			</div>
		</div>
	</div>
	
</template>

<script>

export default {
	props:['setactive'],
	data () {
		return {
			gg:0,
		}
	},
	methods:{
		settingoc:function(){
			this.gg=0
			return this.$store.dispatch("sett");
		},
		del:function(){
			this.gg=1;
		},
		delyes:function(){
			this.gg=0;
			var ServieceIP = this.$store.state.ServieceIP,
				UserDeviceID = this.$store.state.UserDeviceID;
			$.ajax({
	            type: "POST",
	            url: ServieceIP +"smarthome/userDevice/deleteUserDevice",
	            data: {
	                'id': UserDeviceID,
	            },
	            dataType: "json",
	            success: function(data) {
	                if(ISIOS){
						BackIOS(1);
					}else{
						js.BackAndroid(1);
					}
	            },
	            error: function() {
	                if(ISIOS){
						ShowRemind("设备删除失败，请检查网络...");
					}else{
						js.ShowRemind("设备删除失败，请检查网络...");
					}
	            }
	        })
			return this.$store.dispatch("sett");
		},
		delno:function(){
			this.gg=0
		},
		msetactive:function(){
            this.$emit('changesetactive',!this.setactive);
            this.gg=0;
			return this.$store.dispatch("sett");
        },
	},
	computed:{
		subtimeoc:function(){
      		return this.$store.state.subtimeoc
		},
		authlist:function(){
      		return this.$store.state.authlist
		},
		Servieceip: function(){
			return this.$store.state.ServieceIP
		}
	},
}
</script>

<style>
#set_ting{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
}
.setting_xuanzhe{
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 400;
}
.setting_xuanzhe .setting_xuanzhe_bg{
	height: 100%;
	width: 100%;
	left: 0;
	position: absolute;
	background: rgba(0,0,0,.5);
}
.setting_xuanzhe .settingdiv{
	/* height: 13rem; */
	min-height: 6rem;
	padding-bottom: 2.4rem;
	width: 100%;
	background: white;
	position: absolute;
	bottom: 0;
	color: #BDBDBD;
	text-align: center;
}
 .setting_xuanzhe .settingdiv .xsk_tit{
	height: 2rem;
	line-height: 2rem;
}
.setting_xuanzhe .settingdiv .xsk_tit p{
	color: #000;
	font-size: 16px;
	position: relative;
	border-bottom: 1px solid #6acee9
}
.setting_xuanzhe .settingdiv .xsk_tit p.last{
	border-bottom: none
}
.setting_xuanzhe .settingdiv .xsk_submit{
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 2.4rem;
	line-height:2.0rem;
	border-top: 10px solid #DBDBDB;
	font-size: .8rem;
	color: #000000;
}
.subagain{
	width: 10rem;
	height: 4rem;
	position: absolute;
	background: white;
	color: #000;
	text-align:center;
	border-radius: 4px;
	overflow: hidden;
	left: 50%;
	top: 30%;
	margin-left: -5rem;
	font-size: 12px
}
.subagain p{
	height: 2.7rem;
	border-bottom: 6px solid #ddd;
}
.subagain p span{
	display: block;
	height: 1.4rem;
	line-height: 1.4rem;
	font-weight: bold;
}
.subagain .yn{
	height: 1.3rem;
	line-height:1.3rem;
}
.subagain .yn span{
	display: inline-block;
	width: 48%;
	height: 100%;
}
.subagain .yn span.no{
	border-right: 2px solid #ddd;
}
</style>


