<template>
	<div id="app_top">
		
		<div class="back" @touchend="goback">
			<img src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/backbtn.png"/><span>返回</span>
		</div>
		<div class="app_top_tit">
			{{ brandName }}
		</div>
		<div v-if="identity=='' || identity == 'admin'" class="app_top_btn" @touchend='setti'>
			<img src="http://www.ouzhongiot.com/webpage/7A00/7A31A/main/img/setbtn.png">
		</div>
	
	</div>
</template>

<script>

export default {
    data(){
        return {
            asd : 0
        }
    },
    props:['setactive'],
	computed:{
		brandName : function(){
			return this.$store.getters.brandName
		},
        identity:function(){
            return this.$store.state.identity
        },
	},
    methods:{
        goback:function(){
            if(this.setactive==1){
                this.$emit('changesetactive',!this.setactive)
            }else{
                if(ISIOS){
                    BackIOS();
                }else{
                    js.BackAndroid();
                }
            }
            
        },
        setti:function(){
           this.$store.dispatch("sett");
        }
    }
}
</script>

<style>
#app_top{
	position: relative;
    height: 2.1rem;
    border-bottom: 1px solid #98bac7;
    z-index: 99;
}
.back{
    z-index: 201;
    position: absolute;
    padding: .65rem;
    line-height: .8rem;
    font-size: .7rem;
}
.back img{
    height: .8rem;
    vertical-align: top;
    margin-right: 3px;
}
.app_top_tit{
    z-index: 200;
    position: relative;
    line-height: 2.1rem;
    text-align: center;
    font-size: .75rem;
    font-weight: bold;
    color: #1a4a5d
}
.app_top_btn{
    z-index: 201;
    position: absolute;
    height: 2.1rem;
    padding: .625rem .8rem;
    top: 0;
    right: 0;
    -webkit-backface-visibility:hidden;
}
.app_top_btn img{
	height: .85rem;
}
</style>



