export default {
  initinfo:function({ commit }){
    commit('INIT_INFO');
  },
    guan:function({commit}) {
      commit('GUAN')
    },
    kai:function({commit}) {
      commit('KAI')
    },
    jiare:function({commit}) {
      commit('JIARE')
    },
    fengji:function({commit}) {
      commit('FENGJI')
    },
    wenduset:function({commit},e) {
      commit('WENDUSET',e)
    },
    lingdian:function({commit},e) {
      commit('LINGDIAN',e)
    },
    mandu:function({commit},e) {
      commit('MANDU',e)
    },
    alarmdeviation:function({commit}){
      commit('ALARMDEVIATION')
    },
    alarmhigh:function({commit},e){
      commit('ALARMHIGH',e)
    },
    alarmlow:function({commit},e){
      commit('ALARMLOW',e)
    },
    pidsub:function({commit},e) {
      commit('PIDSUB',e)
    },
    durtime:function({commit},e) {
      commit('DURTIME',e)
    },
    
    sett:function({commit}) {
      commit('SETT')
    },
}


