<template>
	<div id='pid_set'>
		<!-- <div class="tempwarn" @touchend='tempwarnactive' :class='theactive == 1?"active":""'>
			<div class="title">温度警报范围</div>
			<div class="setlist">
				<div>
					<span>零点</span><input type="text" ref='overtempHlimit' @blur="overtempHlimitinputblur()" placeholder="-9.9~9.9">
				</div>
				<div>
					<span>满度</span><input type="text" ref='overtempLlimit' @blur="overtempLlimitinputblur()" placeholder="-9.9~9.9">
				</div>
				<div>
					<span>超温报警 偏差</span><input type="text" ref='overtempDvalue' @blur="overtempDvalueinputblur()" placeholder="-9.9~9.9">
					<div class="pcorbj" :class="pcorbjstate?'active':''" @touchend='overtempDvalueinputblur'><div><div></div></div></div><span>设定</span>
				</div>
			</div>
		</div> -->
		<div class="tempwarn" @touchend='tempwarnactive' :class='theactive == 1?"active":""'>
			<div class="title">温度警报范围</div>
			<div class="setlist">
				<div>
					<span>超温报警上限</span><input type="text" ref='overtempHlimit' @blur="overtempHlimitinputblur()" placeholder="0~9999">
				</div>
				<div>
					<span>超温报警下限</span><input type="text" ref='overtempLlimit' @blur="overtempLlimitinputblur()" placeholder="0~9999">
				</div>
				<div>
					<span>超温报警 偏差</span>
					<div class="pcorbj" :class="pcorbjstate?'active':''" @touchend='overtempDvalueinputblur'><div><div></div></div></div><span>设定</span>
				</div>
			</div>
		</div>
		<div class="pid" @touchend='pidactive' :class='theactive == 2?"active":""'>
			<div class="title">PID设置</div>
			<div class="setlist">
				<div>
					<span>PID1</span><input type="text" v-model="pid1p" ref='pidinputP1' v-on:blur="pidblur('P','1')"><input type="text" v-model="pid1i" ref='pidinputI1' v-on:blur="pidblur('I','1')"><input type="text" v-model="pid1d" ref='pidinputD1' v-on:blur="pidblur('D','1')">
				</div>
				<div>
					<span>PID2</span><input type="text" v-model="pid2p" ref='pidinputP2' v-on:blur="pidblur('P','2')"><input type="text" v-model="pid2i" ref='pidinputI2' v-on:blur="pidblur('I','2')"><input type="text" v-model="pid2d" ref='pidinputD2' v-on:blur="pidblur('D','2')">
				</div>
				<div>
					<span>PID3</span><input type="text" v-model="pid3p" ref='pidinputP3' v-on:blur="pidblur('P','3')"><input type="text" v-model="pid3i" ref='pidinputI3' v-on:blur="pidblur('I','3')"><input type="text" v-model="pid3d" ref='pidinputD3' v-on:blur="pidblur('D','3')">
				</div>
				<div>
					<span>PID4</span><input type="text" v-model="pid4p" ref='pidinputP4' v-on:blur="pidblur('P','4')"><input type="text" v-model="pid4i" ref='pidinputI4' v-on:blur="pidblur('I','4')"><input type="text" v-model="pid4d" ref='pidinputD4' v-on:blur="pidblur('D','4')">
				</div>
				<div>
					<span>PID5</span><input type="text" v-model="pid5p" ref='pidinputP5' v-on:blur="pidblur('P','5')"><input type="text" v-model="pid5i" ref='pidinputI5' v-on:blur="pidblur('I','5')"><input type="text" v-model="pid5d" ref='pidinputD5' v-on:blur="pidblur('D','5')">
				</div>
				<div>
					<span>PID6</span><input type="text" v-model="pid6p" ref='pidinputP6' v-on:blur="pidblur('P','6')"><input type="text" v-model="pid6i" ref='pidinputI6' v-on:blur="pidblur('I','6')"><input type="text" v-model="pid6d" ref='pidinputD6' v-on:blur="pidblur('D','6')">
				</div>
				<div>
					<span>PID7</span><input type="text" v-model="pid7p" ref='pidinputP7' v-on:blur="pidblur('P','7')"><input type="text" v-model="pid7i" ref='pidinputI7' v-on:blur="pidblur('I','7')"><input type="text" v-model="pid7d" ref='pidinputD7' v-on:blur="pidblur('D','7')">
				</div>
				<div>
					<span>PID8</span><input type="text" v-model="pid8p" ref='pidinputP8' v-on:blur="pidblur('P','8')"><input type="text" v-model="pid8i" ref='pidinputI8' v-on:blur="pidblur('I','8')"><input type="text" v-model="pid8d" ref='pidinputD8' v-on:blur="pidblur('D','8')">
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	data () {
		return {
			whichactive:1,
		}
	},
	computed:{
		theactive : function(){
    		return this.whichactive
		},
		pcorbjstate : function(){
			return this.$store.state.fAlarmDeviation[4]
		},
		pid1p:function(){
			return this.$store.getters.pid1p
		},
		pid1i:function(){
			return this.$store.getters.pid1i
		},
		pid1d:function(){
			return this.$store.getters.pid1d
		},
		pid2p:function(){
			return this.$store.getters.pid2p
		},
		pid2i:function(){
			return this.$store.getters.pid2i
		},
		pid2d:function(){
			return this.$store.getters.pid2d
		},
		pid3p:function(){
			return this.$store.getters.pid3p
		},
		pid3i:function(){
			return this.$store.getters.pid3i
		},
		pid3d:function(){
			return this.$store.getters.pid3d
		},
		pid4p:function(){
			return this.$store.getters.pid4p
		},
		pid4i:function(){
			return this.$store.getters.pid4i
		},
		pid4d:function(){
			return this.$store.getters.pid4d
		},
		pid5p:function(){
			return this.$store.getters.pid5p
		},
		pid5i:function(){
			return this.$store.getters.pid5i
		},
		pid5d:function(){
			return this.$store.getters.pid5d
		},
		pid6p:function(){
			return this.$store.getters.pid6p
		},
		pid6i:function(){
			return this.$store.getters.pid6i
		},
		pid6d:function(){
			return this.$store.getters.pid6d
		},
		pid7p:function(){
			return this.$store.getters.pid7p
		},
		pid7i:function(){
			return this.$store.getters.pid7i
		},
		pid7d:function(){
			return this.$store.getters.pid7d
		},
		pid8p:function(){
			return this.$store.getters.pid8p
		},
		pid8i:function(){
			return this.$store.getters.pid8i
		},
		pid8d:function(){
			return this.$store.getters.pid8d
		},
	},
	methods:{
		tempwarnactive:function(){
			this.whichactive=1
			$('.tempwarn .setlist').slideDown()
			$('.pid .setlist').slideUp()
		},
		pidactive:function(){
			this.whichactive=2
			$('.tempwarn .setlist').slideUp()
			$('.pid .setlist').slideDown()
		},


		pidblur:function(a,b){
			/*console.log(this.$store.state['f'+a + b]);*/
			var ss = [a,b,this.$refs['pidinput' + a + b].value];
			this.$store.dispatch("pidsub",ss);
		},
		overtempHlimitinputblur:function(){
			var a = this.$refs.overtempHlimit.value
			if(a<=9999 && a>=0 && a){
                this.$refs.overtempHlimit.value = parseInt(a)
                
                this.$store.dispatch("alarmhigh",this.$refs.overtempHlimit.value);
            }else{
            	this.$refs.overtempHlimit.value = ''
            }
		},
		overtempLlimitinputblur:function(){
			var a = this.$refs.overtempLlimit.value
			if(a<=9999 && a>=0 && a){
                this.$refs.overtempLlimit.value = parseInt(a)
                this.$store.dispatch("alarmlow",this.$refs.overtempLlimit.value);
            }else{
            	this.$refs.overtempLlimit.value = ''
            }
		},
		overtempDvalueinputblur:function(){
			/*var a = this.$refs.overtempDvalue.value
			if(a<9.9 && a>-9.9 && a){
                this.$refs.overtempDvalue.value = parseFloat(a).toFixed(1)
                this.$store.dispatch("mandu",this.$refs.overtempDvalue.value);
            }else{
            	this.$refs.overtempDvalue.value = ''
            }*/

            this.$store.dispatch("alarmdeviation");
		}
	}
	
}
</script>

<style>
#pid_set>div .title{
	height: 1.88rem;
	line-height: 1.88rem;
	padding: 0 .6rem;
	font-size: 16px;
	background: #62c3d4;
	background: rgba(98,195,212,.4);
}
#pid_set .setlist{
	width: 92%;
	margin: 0 auto;
	background: rgba(100,186,210,.6);
	border-radius: 6px;
	padding: 0 4%;
	height: 0;
	overflow: hidden;
	-webkit-backface-visibility:hidden;
	/* transition: height .3s linear;
	-webkit-transition: height .3s linear; */
}

#pid_set .setlist>div{
	height: 1.8347rem;
	line-height: 1.6rem;
	border-top: 1px solid #46a0bb;
	margin-top: -1px;
	font-size: 16px;
}
#pid_set .setlist .pcorbj{
	display: inline-block;
	height: 0.94rem;
	width: 2.2rem;
	vertical-align: middle;
	background: #7eb9cf;
	border-radius: .47rem;
	margin-right: 8px;
}
#pid_set .setlist .pcorbj>div{
	height: 0.94rem;
	width: 0.94rem;
	padding: 2px;
	float: left;
}
#pid_set .setlist .pcorbj.active>div{
	float: right;
}
#pid_set .setlist .pcorbj>div>div{
	height: 100%;
	width: 100%;
	border-radius: 100%;
	background: white;
}
#pid_set .setlist div span{
	display: inline-block;
	vertical-align: middle;
	margin-right: 8px;
	font-size: 14px;
}
#pid_set .setlist div:nth-of-type(1){
	border-top: none;
}
#pid_set .setlist div input{
	height: 0.94rem;
	width: 2.77rem;
	background: #7eb9cf;
	text-align: center;
	border-radius: 4px;
	display: inline-block;
	vertical-align: middle;
	margin-left: 12px;
}
#pid_set>div.active{
}
#pid_set>div.active .title{
	background: rgba(100,186,210,.8);
	box-shadow: 0 2px 2px 1px rgba(0,0,0,.16);
}
#pid_set>.tempwarn .setlist{
	height: 5.4rem;
}
#pid_set>.pid .setlist{
	height: 14.4rem;
	display: none;
}
</style>



